import React, { Component, PropTypes } from 'react';
import { connect } from 'react-redux';
import { Link } from 'react-router';
import { editExistingProduct } from 'Actions';
import _ from 'lodash';

const FIELDS = {
	name: {
		type: 'input',
		label: 'Product Name'
	},
	category: {
		type: 'input',
		label: 'Product Category'
	},
	subCategory: {
		type: 'input',
		label: 'Product Subcategory'
	},
	brand: {
		type: 'input',
		label: 'Product Brand'
	},
	locationOfProduct: {
		type: 'input',
		label: 'Location of Product'
	},
	manufacturer: {
		type: 'input',
		label: 'Product Manufacturer'
	},
	manufacturerCountry: {
		type: 'input',
		label: "Product Manufacturer's Country"
	},
	ingredients: {
		type: 'input',
		label: 'Product Ingredients'
	},
	price: {
		type: 'input',
		label: 'Product Price'
	},
	dosageForm: {
		type: 'input',
		label: 'Product Dosage Form'
	},
	typeOfProduct: {
		type: 'input',
		label: 'Product Type'
	},
	quantity: {
		type: 'input',
		label: 'Product Quantity'
	},
	description: {
		type: 'textarea',
		label: 'Product Description'
	}
};

class EditProductForm extends Component {

	static contextTypes = {
		router: PropTypes.object
	};

	constructor(props) {
  	super(props);

		// pull out the current values of the product to edit
		const { name, category, subCategory, brand,
						locationOfProduct, manufacturer, manufacturerCountry,
						ingredients, price, dosageForm, typeOfProduct, quantity,
						description } = this.props.singleProduct;

		// give local state a key value pair identical to each key value pair
		// in the product to edit
  	this.state = { name, category, subCategory, brand,
									 locationOfProduct, manufacturer, manufacturerCountry,
									 ingredients, price, dosageForm, typeOfProduct, quantity,
									 description, errors: {} };

		this.renderField = this.renderField.bind(this);
		this.handleFieldEdit = this.handleFieldEdit.bind(this);
		this.handleSubmit = this.handleSubmit.bind(this);
  }

	validate(values) {

		//Check for errors and assign errors object to local state

		return new Promise((resolve, reject) => {

			const errors = {};
			const numberRegex = /^-?\d+\.?\d*$/;

			_.each(FIELDS, (config, field) => {
				if (!values[field]) {
					errors[field] = `Please Enter ${config.label}`;
				}
				if (!numberRegex.test(values['price'])) {
				    errors['price'] = 'Please enter a number';
				}
				if (!numberRegex.test(values['quantity'])) {
					errors['quantity'] = 'Please enter a number';
				}
			});

			this.setState({
				errors
			}, () => {
				resolve(this.state);
			});

		});


	}

	handleFieldEdit(event) {

		this.setState({
			[event.target.name]: event.target.value
		});

	}

	handleSubmit(event) {

		event.preventDefault();

		this.validate(this.state).then(() => {
			if (_.isEmpty(this.state.errors)) {

				let propsToSend = this.state;
				propsToSend.productId = this.props.singleProduct._id;
				const jsonProps = JSON.stringify(propsToSend);

				this.props.editExistingProduct(jsonProps).then(() => {
					this.context.router.push('/inventory');
				});

			} else {
				return;
			}
		});

	}

	renderField(fieldConfig, field) {

		//This will return the fields for editing a product's information
		//the value of each field will be set according to singleProduct in global state
		//the singleProduct's information is accessible and changeable in local state
		return (
			<div key={field} className="col-lg-6 col-md-6">
				<fieldset id="newProductFields" className="form-group">
					<label>{fieldConfig.label}</label>
					<fieldConfig.type name={field} className="form-control" type="text" value={this.state[field]} onChange={this.handleFieldEdit} />
					<div className="text-help">
						{this.state.errors[field] ? this.state.errors[field] : ''}
					</div>
				</fieldset>
			</div>

		);

	}

	render() {

			return (
				<div className="container" id="newProductForm">
					<div className="row">

						<h3 className="center">Edit Product Info</h3>

				    <form onSubmit={this.handleSubmit}>
				      {_.map(FIELDS, this.renderField)}
				      <button id="createProductBtn" type="submit" className="btn btn-primary">Update Product</button>
							<Link to="/allProducts" id="cancelProductBtn" className="btn btn-danger">Cancel</Link>
				    </form>

					</div>
				</div>
			);
	}
}

function mapStateToProps(state) {
	return { singleProduct: state.products.singleProduct };
}

export default connect(mapStateToProps, { editExistingProduct })(EditProductForm);



