import _ from 'lodash';
import React, { Component, PropTypes } from 'react';
import { Link } from 'react-router';
import { reduxForm } from 'redux-form';
import * as actions from 'Actions';

const FIELDS = {
	name: {
		type: 'input',
		label: 'Product Name'
	},
	category: {
		type: 'input',
		label: 'Product Category'
	},
	subCategory: {
		type: 'input',
		label: 'Product Subcategory'
	},
	brand: {
		type: 'input',
		label: 'Product Brand'
	},
	locationOfProduct: {
		type: 'input',
		label: 'Location of Product'
	},
	manufacturer: {
		type: 'input',
		label: 'Product Manufacturer'
	},
	manufacturerCountry: {
		type: 'input',
		label: "Product Manufacturer's Country"
	},
	ingredients: {
		type: 'input',
		label: 'Product Ingredients'
	},
	price: {
		type: 'input',
		label: 'Product Price'
	},
	dosageForm: {
		type: 'input',
		label: 'Product Dosage Form'
	},
	typeOfProduct: {
		type: 'input',
		label: 'Product Type'
	},
	quantity: {
		type: 'input',
		label: 'Product Quantity'
	},
	description: {
		type: 'textarea',
		label: 'Product Description'
	}
};


class NewProductForm extends Component {

	static contextTypes = {
		router: PropTypes.object
	};

	onSubmit(props) {

		props.owner = this.props.activeUser._id; //Adds owner property to this props based on active user from redux state

		const jsonProps = JSON.stringify(props); //converts properties into json before sending them to the server

		this.props.createProduct(jsonProps).then(() => { //call action creator to submit the new product
			this.context.router.push('/inventory'); //upon success return to the inventory
		});

	}

	renderField(fieldConfig, field) {

		const fieldHelper = this.props.fields[field];

		return (
			<div id="productFormField" className="col-lg-6 col-md-6">
			  <fieldset key={field} id="newProductFields" className="form-group">

					<label>{fieldConfig.label}</label>

					<fieldConfig.type className="form-control" type="text" placeholder={`${field === 'price' ? 'USD' : ''}`} {...fieldHelper} />

					<div className="text-help">
						{fieldHelper.touched ? fieldHelper.error : ''}
					</div>

			  </fieldset>
			</div>
	  );

	}

	render() {

		const { handleSubmit } = this.props;

		return (
			<div className="container" id="newProductForm">
				<div className="row">

					<h3 className="center">Create a New Product</h3>

			    <form onSubmit={handleSubmit(this.onSubmit.bind(this))}>
			      {_.map(FIELDS, this.renderField.bind(this))}
			      <button id="createProductBtn" className="btn btn-primary" type="submit">Create Product</button>
			    	<Link
							to="/allProducts"
							id="cancelProductBtn"
				      className="btn btn-danger">Cancel</Link>
			    </form>

				</div>
			</div>
		);
	}

}

function validate(values) {
	const errors = {};
	const numberRegex = /^-?\d+\.?\d*$/;



	_.map(FIELDS, (config, field) => {
		if (!values[field]) {
			errors[field] = `Please Enter ${config.label}`;
		}

		if (!numberRegex.test(values['price'])) {
		    errors['price'] = 'Please enter a number';
		}
		if (!numberRegex.test(values['quantity'])) {
			errors['quantity'] = 'Please enter a number';
		}
	});

	return errors;
}

function mapStateToProps(state) {
	return { activeUser: state.user.activeUser };
}

export default reduxForm({
	form: 'CreateNewProduct',
	fields: _.keys(FIELDS), //This lodash method returns an array of strings for each key in the fields object ['name', 'category', ...etc]
	validate
											   //These configurations will be added to the application state, so reduxForm is very similar to the connect function.
											   //connect: first argument is mapStateToProps, second is mapDispatchToProps
											   //reduxForm: 1st is form configuration, 2nd is mapStateToProps, 3rd is mapDispatchToProps

}, mapStateToProps, actions)(NewProductForm);



