import React, {Component} from 'react';
import {browserHistory} from 'react-router';
import { connect } from 'react-redux';
import SingleProduct from 'SingleProduct';
import { fetchSingleProduct, deleteExistingProduct } from 'Actions';
import DeleteProductBtn from 'DeleteProductBtn';

class ProductProfile extends Component {
	constructor(props) {
		super(props);
		this.handleDelete = this.handleDelete.bind(this);
	}

	componentWillMount() {
		this.props.fetchSingleProduct(this.props.params.id);
	}

	handleDelete(productId) {
		this.props.deleteExistingProduct(productId).then(() => {
			browserHistory.push('/inventory');
		});
	}

	render() {

		const { singleProduct } = this.props;

		if (this.props.singleProduct) {

			return (
				<div id="productProfile">
					<div className="container">
						<div className="row">
							<div className="col-lg-12 col-md-12">
								<div className="panel panel-default">
									<div className="panel-heading"><h3 className="center">{singleProduct.name} Profile</h3></div>
									<div className="table-responsive">
										<table className="table table-condensed">
											<tbody>
												<tr>
													<th>SKU #</th>
													<th>Name</th>
													<th>Category</th>
													<th>Sub-Category</th>
												</tr>
												<tr>
													<td>{singleProduct._id}</td>
													<td>{singleProduct.name}</td>
													<td>{singleProduct.category}</td>
													<td>{singleProduct.subCategory}</td>
												</tr>
												<tr>
													<th>Brand</th>
													<th>Location Of Product</th>
													<th>Manufacturer</th>
													<th>Manufacturer's Country</th>
												</tr>
												<tr>
													<td>{singleProduct.brand}</td>
													<td>{singleProduct.locationOfProduct}</td>
													<td>{singleProduct.manufacturer}</td>
													<td>{singleProduct.manufacturerCountry}</td>
												</tr>
												<tr>
													<th>Ingredients</th>
													<th>Price</th>
													<th>Dosage Form</th>
													<th>Type of Product</th>
												</tr>
												<tr>
													<td>{singleProduct.ingredients}</td>
													<td>${singleProduct.price}</td>
													<td>{singleProduct.dosageForm}</td>
													<td>{singleProduct.typeOfProduct}</td>
												</tr>
												<tr>
													<th>Quantity</th>
													<th>Description</th>
												</tr>
												<tr>
													<td>{singleProduct.quantity}</td>
													<td>{singleProduct.description}</td>
												</tr>
											</tbody>
										</table>
									</div>
								</div>
							</div>

							<div className="col-lg-4 col-md-4 col-lg-offset-4">
								<i id="backArrow" onClick={() => browserHistory.push('/inventory')} className="fa fa-arrow-circle-o-left fa-4x" aria-hidden="true" ></i>
								<DeleteProductBtn productToDelete={singleProduct._id} handleDelete={this.handleDelete} />
							</div>

						</div>

					</div>
				</div>
			);

		}

	}
};

function mapStateToProps(state) {
	return { singleProduct: state.products.singleProduct };
}


export default connect(mapStateToProps, { fetchSingleProduct, deleteExistingProduct })(ProductProfile);



