import React, { Component } from 'react';
import { connect } from 'react-redux';
import * as actions from 'Actions';

class Signout extends Component {

	componentWillMount() {
		this.props.signoutUser(); //Signs out user as soon as they hit this route
		//This strategy allows the use of a message to show the user before they leave
	}

	render() {
		return (
			<div id="homeDiv">
			   <h3 className="marginTop marginBottom text-center">Point of Sale System</h3>
			    <h5 className="marginBottom text-center">Optimize and grow your pharmacy with the Hello Bacsi P.O.S. System</h5>

			    <div className="row">
			      <div className="columns medium-6 large-4 small-centered">

			        <ul id="marketingFeatures" className="text-center marginBottom">
			          <li>Upload Your inventory</li>
			          <li>Make Sales</li>
			          <li>Track Revenue</li>
			          <li>Strategically plan future orders</li>
			          <li>Maximize profits</li>
			        </ul>

			        <img id="homeBacsiLogo" className="marginBottom text-center" src="../css/hello_bacsi.png"></img>

			      </div>
			    </div>
			</div>
		);
	}

}

export default connect(null, actions)(Signout);



