import React, {Component} from 'react';
import { connect } from 'react-redux';
import {Link} from 'react-router';
import { addToCart, calculateCartTotals } from 'Actions';
import axios from 'axios';
import _ from 'underscore';

class SingleProduct extends Component {

	render() {
		return (
			<div id="singleProduct">
				<div className="row">

					<div id="examineIcon" className="col-lg-12 col-md-12">
						<Link to={`/productProfile/${this.props.id}`}><i className="fa fa-arrows-alt"></i></Link>
					</div>

					<div id="pricetag" className="col-lg-12 col-md-12">
						<i className="fa fa-tag fa-4x" onClick={this.handleAddProduct.bind(this)}></i>
					</div>

					<div className="col-lg-12 col-md-12">
						<p id="productName" className="bold">{this.props.name}</p>
					</div>

				</div>
			</div>
		);
	}

	handleAddProduct() {

		this.props.addToCart(this.props.product);
		this.props.calculateCartTotals();

	}

}

function mapStateToProps(state) {
	return { cart: state.cart.currentCart, activeUser: state.user.activeUser };
}


export default connect(mapStateToProps, { addToCart, calculateCartTotals })(SingleProduct);



