import React, { Component } from 'react';
import { connect } from 'react-redux';
import { hideReceipt, clearCart } from 'Actions';
import moment from 'moment';

class Receipt extends Component {

	render() {

		var createdDate = moment(this.props.lastSale.date).format('MMMM Do YYYY, h:mm:ss a');
		//formats date with Moment.js

		return (
			<div className="receipt">
				<div className="container-fluid">
				<div className="row">
					<div className="col-lg-12 col-md-12">
						<div className="panel panel-default">
						  <div className="panel-heading">
						  	<h5 className="center">Sale Summary</h5>
						  </div>
						  <div className="saleItems">
							  <table className="table">
							  	<tbody>
								    {this.renderSaleItems()}
									</tbody>
							  </table>
						  </div>
						  <div className="panel-footer">
						  	<table className="table">
							  	<tbody>
								    {this.renderSaleTotals()}
								</tbody>
							</table>

							<button onClick={this.hideReceipt.bind(this)} className="btn btn-success">
							<i className="fa fa-check-square-o fa-3x" aria-hidden="true"></i></button>

						  </div>
						</div>
					</div>
				</div>
				</div>
			</div>
		);

	}

	renderSaleItems() {

		return this.props.lastSale.items.map(item => {

			return (
				<tr key={item._id}>

					<td>{item.itemName}</td>
					<td>{item.cartQuantity}</td>
					<td>X</td>
					<td>${item.price}</td>
					<td>=</td>
					<td>${item.cartQuantity * item.price}</td>
					<td><i className="fa fa-check" aria-hidden="true"></i></td>

			  </tr>
			);
		});

	}

	renderSaleTotals() {

		return (
			<tr>
				<td>Discount: 0%</td>
				<td>Tax: {this.props.tax}%</td>
				<td>Total: ${this.props.lastSale.total + (this.props.lastSale.total * this.props.tax/100)}</td>
			</tr>
		);
	}

	hideReceipt() {
		this.props.clearCart(); //clears the global cart values
		this.props.hideReceipt(); //hides receipt by setting showReceipt to false which shows Checkout.jsx again
	}

}

function mapStateToProps(state) {
	return { lastSale: state.sale.lastSale, tax: state.cart.tax };
}

export default connect(mapStateToProps, { hideReceipt, clearCart })(Receipt);



