import { FETCH_ALL_PRODUCTS, SEARCH_ALL_PRODUCTS, SEARCH_HERBALS, SEARCH_PHARMA, FETCH_SINGLE } from '../actions/index';


const INITIAL_STATE = { all: [], pharma: [], herbals: [], singleProduct: {} };

export default function(state = INITIAL_STATE, action) {

	switch(action.type) {

		case FETCH_ALL_PRODUCTS:

			//You need a different action type for searching all products.
			//Right now searching all products calls FETCH_PRODUCTS as its action type
			//This sets both pharma and herbals to a length of 0 after it's done.
			//You want to keep those in the redux state, not delete them. Create a
			//SEARCH_ALL_PRODUCTS action type call that when you search all products.
			//I would also rename FETCH_HERBALS and FETCH_PHARMA to SEARCH_HERBALS and
			//SEARCH_PHARMA because they only get called during a search now.

			let pharma = action.payload.data.filter(product => {
				return product.category === 'pharmaceutical';
			});

			let herbals = action.payload.data.filter(product => {
				return product.category === 'herbal';
			});

			return {...state, all: action.payload.data, pharma, herbals };

		case SEARCH_ALL_PRODUCTS:
			return {...state, all: action.payload.data};

		case SEARCH_HERBALS:
			return {...state, herbals: action.payload.data};

		case SEARCH_PHARMA:
			return {...state, pharma: action.payload.data};

		case FETCH_SINGLE:
			return {...state, singleProduct: action.payload.data}

		default:
			return state;

	}
}



