import React, { Component, PropTypes } from 'react';
import { connect } from 'react-redux';
import { Link } from 'react-router';
import * as actions from 'Actions';
import NewProductForm from 'NewProductForm';
import EditProductForm from 'EditProductForm';

class AllProducts extends Component {

	static contextTypes = {
		router: PropTypes.object
	};

  componentWillMount() {
		this.props.fetchProducts(); //Fetches all products when the page loads
	}

	handleEditProduct(productId) {

		this.props.fetchSingleProduct(productId).then(() => {
			this.context.router.push('/editProduct');
	  });

	}

	render() {
		return (
			<div className="allProducts">

			  <div className="container">
			  	{this.renderProducts()}
			  </div>

			</div>
		);
	}

	renderProducts() {
		//break down into smaller components

			return (
				<div className="allProductsShown">
					<div className="row">
						<div className="col-lg-12 col-md-12">
							<div className="panel panel-default">
							<div className="panel-heading">
								<div className="row">
									<div className="col-lg-6 col-md-6 col-sm-6 col-xs-6">
										<h4>ALL PRODUCTS</h4>
									</div>
									<div className="col-lg-6 col-md-6 col-sm-6 col-xs-6">
										<p><Link to="/addProduct" id="addProductBtn" className="btn btn-primary">Add a Product</Link></p>
									</div>
								</div>
							</div>
								<div className="table-responsive">
									<table className="table">
						    		<tbody>
						    			<tr>
						    				<th>Name</th>
						    				<th>SKU #</th>
						    				<th>Category</th>
						    				<th>Price</th>
						    				<th>Quantity</th>
						    				<th>Edit</th>
						    			</tr>
						    			{this.renderProductsTable()}
										</tbody>
						    	</table>
								</div>
							</div>
						</div>
					</div>
				</div>
			);

	}

	renderProductsTable() {
		//break down into smaller components

		return this.props.allProducts.map((product) => {
			return (
				<tr key={product._id}>
					<td>{product.name}</td>
					<td>{product._id}</td>
					<td>{product.category}</td>
					<td>${product.price}</td>
					<td>{product.quantity}</td>
					<td>
						{/*When it is time to add the admin feature to a user's profile,
							use this for the edit icon:

							<i onClick={this.props.activeUser.admin ? this.handleEditProduct.bind(this, product._id) : null}
									 id="editProductIcon"
									 className="fa fa-pencil-square-o"
									 aria-hidden="true"></i>*/}
						<i onClick={this.handleEditProduct.bind(this, product._id)}
							 id="editProductIcon"
							 className="fa fa-pencil-square-o"
							 aria-hidden="true"></i>
					</td>
				</tr>
			);
		});

	}



}

function mapStateToProps(state) {
	return { allProducts: state.products.all };
}

export default connect(mapStateToProps, actions)(AllProducts);



