import React, { Component } from 'react';
import { connect } from 'react-redux';
import { makeSale, deleteProductInCart, calculateCartTotals } from 'Actions';
import moment from 'moment';
import Receipt from 'Receipt';

var Checkout = React.createClass({

	getInitialState: function() {
	    return {
	    	saleMade: true
	    };
	},

	render: function() {

		//if the user makes a sale, the receipt component will replace the cart
		if (this.props.showReceipt) {

			return <Receipt />;

		} else {
			//Here is the current time if I want to include it
			// var today = new Date();
			// today = moment(today).format('MMMM Do YYYY, h:mm:ss a');
			return (
				<div className="checkout">
					<div className="row">

						<div className="col-lg-12 col-md-12">
							<div className="panel panel-default">
							  <div className="panel-heading">{this.renderPanelHeading()}</div>
							  <div className="cartTableContainer">
								  <table className="table">
								  	<tbody>
									    {this.renderProducts()}
									</tbody>
								  </table>
							  </div>
							  <div className="panel-footer">
							  	<table className="table">
								  	<tbody>
									    {this.renderTotals()}
									</tbody>
								</table>
								<button onClick={this.handlePay} className="btn btn-success">Pay</button>
							  </div>
							</div>
						</div>

					</div>
				</div>
			);
	    }
	},

	renderPanelHeading: function() {

		if (this.props.cart.length === 0) {
			return [
				<h5>Add items to make a sale</h5>,
				<p><i className="fa fa-plus-square-o fa-3x" aria-hidden="true"></i></p>
			];
		} else {
			return [
				<h5>Active Cart</h5>,
				<p><i className="fa fa-cart-plus fa-3x" aria-hidden="true"></i></p>
			];
		}

	},

	renderProducts: function() {

		if (this.props.cart.length > 0) {

			return this.props.cart.map(product => {

				return (
					<tr key={product._id}>

						<td>{product.name}</td>
					  <td>{product.cartQuantity}</td>
						<td>X</td>
					  <td>${product.price}</td>
						<td>=</td>
						<td>${product.cartQuantity * product.price}</td>

					  <td id="cartDelete" onClick={() => {
					  	this.props.deleteProductInCart(product);
					  	this.props.calculateCartTotals();
				  	  }}><i className="fa fa-trash-o" aria-hidden="true"></i></td>

			    </tr>
				);

			});
		}

	},

	renderTotals: function() {
		//the total here is calculated on the fly using tax, but will not be recorded in the sales themselves
		//the reasoning being is because we only calculated revenue from the database
		return (
			<tr>
				<td>Subtotal: ${this.props.cartSubtotal}</td>
				<td>Discount: 0%</td>
				<td>Tax: {this.props.tax}%</td>
				<td>Total: ${this.props.cartTotal + (this.props.cartTotal * this.props.tax/100)}</td>
			</tr>
		);

	},

	handlePay: function() {

		var props = {
			user_id: this.props.activeUser._id, //This sale will belong to the user that is logged in (global state)
			items: [], //All the items in the cart will be added to the sale below (global state)
			total: this.props.cartTotal //Total price of the sale is drawn from the global state
		}


		this.props.cart.map((product) => {
			var item = {};
			item.name = product.name;
			item.product_id = product._id;
			item.cartQuantity = product.cartQuantity;
			item.priceValue = product.price;
			props.items.push(item);
		});

		var jsonProps = JSON.stringify(props); //converts properties into json before sending them to the server

		this.props.makeSale(jsonProps);
	}

});

function mapStateToProps(state) {

	return {
		cart: state.cart.currentCart,
		cartTotal: state.cart.cartTotal,
		cartSubtotal: state.cart.cartSubtotal,
		activeUser: state.user.activeUser,
		showReceipt: state.sale.showReceipt,
		tax: state.cart.tax
	};

}


export default connect(mapStateToProps, { makeSale, deleteProductInCart, calculateCartTotals })(Checkout);



