import React, {PropTypes} from 'react';
import {Link} from 'react-router';

const PostAuthNavTabs = ({activeUser}) => {
  return (
    <div className="navbar-collapse collapse">
      <ul className="nav navbar-nav navbar-left">
        <li><Link to="/inventory">Inventory</Link></li>
        <li><Link to="/allProducts">Products</Link></li>
        <li><Link to="/reporting">Reporting</Link></li>
      </ul>
      <ul className="nav navbar-nav navbar-right">
  			<li><Link to="/userProfile">{activeUser.profile.name}</Link></li>
  			<li><Link to="/signout">Signout</Link></li>
  		</ul>
    </div>
  );
};

PostAuthNavTabs.propTypes = {
  activeUser: PropTypes.object.isRequired
};

export default PostAuthNavTabs;



