import React, { Component, PropTypes } from 'react';
import { Link } from 'react-router';
import * as actions from 'Actions';
import { connect } from 'react-redux';

class UserProfile extends Component {

	constructor(props) {
    	super(props);

    	this.state = {
	      tax: 0
    	};
  }

	handleTaxChange(event) {
		this.setState({
			tax: event.target.value
		});
	}

	onSubmit(event) {
		event.preventDefault();
		//this doesnt seem to submit yet
		this.props.setCartTax(this.state.tax);
	}

	render() {

		return (
			<div id="userProfile">
				<div className="container">
					<div className="row">
						<div className="col-lg-12 col-md-12">
							<div className="panel panel-default">
								<div className="panel-heading"><h3 className="center">User Profile</h3></div>

									<div className="table-responsive">
										<table className="table">
											<tbody>
												<tr>
													<th>Username</th>
													<th>Email</th>
													<th>User ID</th>
													<th>Address</th>
												</tr>
												<tr>
													<td>{this.props.activeUser.profile.name}</td>
													<td>{this.props.activeUser.email}</td>
													<td>{this.props.activeUser._id}</td>
													<td>{this.props.activeUser.address}</td>
												</tr>
											</tbody>
										</table>
									</div>

									<div className="panel-footer">
										<h4>Current Store Tax Rate: {this.props.tax}%</h4>
										<form onSubmit={this.onSubmit.bind(this)}>
											<fieldset className="form-group">
												<label>Set Store Tax</label>

												<input value={this.state.tax}
													onChange={this.handleTaxChange.bind(this)}
													type="number"
													className="form-control"/>

											</fieldset>
											<button type="submit" className="btn btn-primary">Set</button>
								  	</form>
									</div>

							</div>
						</div>
					</div>
				</div>
			</div>
		);
	}

}



function mapStateToProps(state) {
	return { activeUser: state.user.activeUser, tax: state.cart.tax };
}


export default connect(mapStateToProps, actions)(UserProfile);



