import React, { Component } from 'react';
import { connect } from 'react-redux';
import { fetchProducts, fetchHerbals, fetchPharma } from 'Actions';
import ProductSearch from 'ProductSearch';
import ProductFilter from 'ProductFilter';
import SingleProduct from 'SingleProduct';
import Checkout from 'Checkout';

class StoreFrontContainer extends Component {

  constructor(props) {
  	super(props);

  	this.state = {
      showAll: true,
	    showPharma: false,
	    showHerbals: false
  	};
    this.handleFilter = this.handleFilter.bind(this);
  }

  componentWillMount() {
		this.props.fetchProducts(); //Fetches all products when the page loads
	}

  handleFilter(event) {

		if (event.target.innerHTML === "All") {

			this.setState({
				showAll: true,
				showPharma: false,
				showHerbals: false
			});

		} else if (event.target.innerHTML === "Pharmaceuticals") {

			this.setState({
				showAll: false,
				showPharma: true,
				showHerbals: false
			});


		} else if (event.target.innerHTML === "Herbals") {

			this.setState({
				showAll: false,
				showPharma: false,
				showHerbals: true
			});

		}

	}

  renderProducts() {
    if (this.state.showAll) {
      return this.props.allProducts.map(product => {
        return (
          <div key={product._id} className="col-lg-3 col-md-3 col-sm-3 col-xs-6" id="productThumbnail">
            <SingleProduct
               key={product._id}
               product={product}
               id={product._id}
               name={product.name}
               category={product.category.name}
               price={product.price} />
          </div>
         );
      });
    } else if (this.state.showPharma) {
      return this.props.pharma.map(product => {
        return (
          <div key={product._id} className="col-lg-3 col-md-3 col-sm-3 col-xs-6" id="productThumbnail">
            <SingleProduct
               key={product._id}
               product={product}
               id={product._id}
               name={product.name}
               category={product.category.name}
               price={product.price} />
          </div>
         );
      });
    } else if (this.state.showHerbals) {
      return this.props.herbals.map(product => {
        return (
          <div key={product._id} className="col-lg-3 col-md-3 col-sm-3 col-xs-6" id="productThumbnail">
            <SingleProduct
               key={product._id}
               product={product}
               id={product._id}
               name={product.name}
               category={product.category.name}
               price={product.price} />
          </div>
         );
      });
    }

  }

  renderLoader() {
    return <h3 id="loadingMessage" className="text-center">Loading products...</h3>;
  }

  render() {

		return (
			<div>

				<div className="inventory">

					<div className="container">
						{/* This is the main row for '/inventory' */}
						<div className="row">
							{/* This column starts left half of the screen */}
							<div className="col-lg-6 col-md-6">

								<div className="row">
									<div className="col-lg-12 col-md-12">


                    <ProductFilter
                      showAll={this.state.showAll}
                      showPharma={this.state.showPharma}
                      showHerbals={this.state.showHerbals}
                      handleFilter={this.handleFilter} />

										<ProductSearch
                      showAll={this.state.showAll}
                      showPharma={this.state.showPharma}
                      showHerbals={this.state.showHerbals} />

									</div>

									<div className="col-lg-12 col-md-12">
                    {/*This won't render the products as you search for them because
                      this checks that all 3 categories have a length check reducer*/}
										{this.props.allProducts.length && this.props.pharma.length && this.props.herbals.length ?
                    this.renderProducts() : this.renderLoader()}

									</div>
								</div>

							</div>
							{/* End left half of screen */}

							{/* This column starts right half of the screen */}
							<div className="col-lg-6 col-md-6">
								<Checkout/>
							</div>
							{/* Ends right half of screen */}

						</div>
					</div>
				</div>
			</div>
		);
	}

}

function mapStateToProps(state) {
	//delete active user later
	return {
    allProducts: state.products.all,
    herbals: state.products.herbals,
    pharma: state.products.pharma,
    activeUser: state.user.activeUser };
}

export default connect(mapStateToProps, { fetchProducts, fetchHerbals, fetchPharma })(StoreFrontContainer);



