import React, {PropTypes} from 'react';
import {Link} from 'react-router';

const WeeksTopItemsRow = ({ item, numSold }) => {
  return (
    <tr>
      <td>{item.itemName}</td>
      <td>{item.itemId}</td>
      <td>${item.price}</td>
      <td>{numSold}</td>
      <td><Link to={`/productProfile/${item._id}`}><i className="fa fa-arrows-alt"></i></Link></td>
    </tr>
  );
}
WeeksTopItemsRow.propTypes = {
  item: PropTypes.object.isRequired,
  numSold: PropTypes.number.isRequired
};

export default WeeksTopItemsRow;



